import React, { useEffect } from "react";
import SunEditor from "suneditor-react";
import "suneditor/dist/css/suneditor.min.css";
import history from "../../history";

export default function CreatePage() {
  return (
    <div className="container-fluid" style={{}}>
      
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">Sửa bài viết</h4>
      </div>
      <div className="card shadow mb-4">
        <div className="card-body">
          <section className="content">
            <div className="row">
              <div className="col-md-12 col-xs-12">
                <div id="messages" />
                <div className="box">
                  <form role="form" method="post">
                    <div className="box-body">
                        <div className="form-group">
                          <label>Ảnh: &nbsp; </label>
                          <img
                            src="https://picsum.photos/200/300"
                            width={150}
                            height={150}
                          />
                        </div>
                        <div className="form-group">
                          <div className="kv-avatar">
                            <div>
                              <button
                                type="button"
                                className="btn btn-primary btn-sm"
                                data-toggle="modal"
                                data-target="#uploadModalBlog"
                              >
                                <i className="fa fa-plus" /> Upload ảnh
                              </button>
                            </div>
                          </div>
                        </div>
                        <div className="form-group">
                          <label htmlFor="product_name">Tên bài viết</label>
                          <input
                            type="text"
                            className="form-control"
                            id="txtTitle"
                            placeholder="Nhập tên bài viết"
                            autoComplete="off"
                            name="txtTitle"
                            
                          />
                        </div>

                        <div className="form-group">
                          <label htmlFor="product_name">Trạng thái</label>
                          <select
                            name="txtPublished"
                            id="input"
                            className="form-control"
                          >
                            <option value={1}>Hiển thị</option>
                            <option value={0}>Lưu tạm</option>
                          </select>
                        </div>
                      </div>
                      <div className="form-group">
                        <label htmlFor="product_name">Mô tả bài viết</label>
                        <textarea
                          name="txtSumary"
                          id="input"
                          className="form-control"
                          rows={3}
                        />
                      </div>
                      <div class="form-group">
                        <label for="product_name">Nội dung bài viết</label>
                        <div className="editor">
                          <SunEditor
                            showToolbar={true}
                            setDefaultStyle="height: auto"
                            setOptions={{
                              buttonList: [
                                [
                                  "undo",
                                  "redo",
                                  "font",
                                  "fontSize",
                                  "formatBlock",
                                  "paragraphStyle",
                                  "blockquote",
                                  "bold",
                                  "underline",
                                  "italic",
                                  "fontColor",
                                  "textStyle",
                                  "outdent",
                                  "align",
                                  "horizontalRule",
                                  "list",
                                  "lineHeight",
                                  "table",
                                  "link",
                                  "image",
                                  "video",
                                  "audio",
                                  "fullScreen",
                                  "preview",
                                ],
                              ],
                            }}
                          />
                        </div>
                      </div>
                  </form>
                  <div className="box-footer">
                    <button type="submit" className="btn btn-info   btn-sm">
                      <i className="fas fa-save" /> Lưu
                    </button>
                    <button
                                            onClick={()=>{history.goBack()}}

                      type="button"
                      className="btn btn-warning   btn-sm"
                      style={{ marginLeft: "10px" }}
                    >
                      <i className="fas fa-arrow-left" /> Trở về
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}
